import { lazy, Suspense } from "react";
import {
  Switch,
  Route,
  useRouteMatch,
  Redirect,
  useLocation,
} from "react-router-dom";
import { Spinner } from "@/components";

import styles from "@/scss/main.module.scss";

const Projects = lazy(
  () => import("@/views/Cabinet/Projects" /* webpackChunkName: "Projects" */)
);
const Billing = lazy(
  () => import("@/views/Cabinet/Billing" /* webpackChunkName: "Billing" */)
);
const Settings = lazy(
  () => import("@/views/Cabinet/Settings" /* webpackChunkName: "Settings" */)
);
const Stats = lazy(
  () => import("@/views/Cabinet/Stats" /* webpackChunkName: "Stats" */)
);

type Props = {};

const Cabinet: React.FC<Props> = () => {
  const { url } = useRouteMatch();
  const { pathname } = useLocation();
  return (
    <div className={'page'}>
      <div className={'pageContent'}>
        <Suspense fallback={<Spinner />}>
          <Switch>
            {/** Removing trailing slashes from the URL*/}
            <Redirect from="/:url*(/+)" to={pathname.slice(0, -1)} />

            <Route path={`${url}/projects`} component={Projects} />
            <Route path={`${url}/stats`} component={Stats} />
            <Route path={`${url}/billing`} component={Billing} />
            <Route path={`${url}/settings`} component={Settings} />
            <Route
              exact
              path={`${url}/`}
              component={() => <Redirect to={`${url}/projects`} />}
            />
          </Switch>
        </Suspense>
      </div>
    </div>
  );
};

export default Cabinet;
