import { UserProjectType } from "@/MST/user";
import { memo } from "react";
import { Link, useRouteMatch } from "react-router-dom";
import Button from "../elements/Button";
import styles from "./ProjectTile.module.scss";

type Props = UserProjectType & { onDelete: (projectId: string) =>void };

const ProjectTile: React.FC<Props> = ({
  _id,
  projectName,
  projectCreationDate,
  projectURL,
  onDelete,
}) => {
  const { url } = useRouteMatch();

  const onDeleteClick = () => {
    onDelete(_id || '');
  }

  return (
    <div className={styles.projectTile}>
      <div className={styles.projectDetails}>
        {" "}
        <Link to={`${url}/${_id}`}>
          <div className={styles.projectName}>{projectName}</div>
          <div className={styles.projectURL}>URL: {projectURL}</div>
          <div className={styles.projectURL}>
            Created: {projectCreationDate}
          </div>
        </Link>
      </div>

      <div className={styles.projectControls}>
        <Button icon="icon_delete" text={"Delete project"} onClick={onDeleteClick}/>
      </div>
    </div>
  );
};

export default memo(ProjectTile);
